import { useEffect, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import VideoRecorder from './components/VideoRecorder/VideoRecorder';
import CVUpload from './components/CVUpload/CVUpload';

function App() {
  const [videoKeys, setVideoKeys] = useState(undefined);

  const getKeys = async () => {
    try {
      const keys = await fetch(`/video-key`);
      const keysFormatted = await keys.json();

      setVideoKeys(keysFormatted);
    } catch (e) {
      console.error(e);
    }
  };

  useEffect(() => {
    getKeys();
  }, []);

  return (
    <div>
      <Routes>
        <Route path="/" element={<CVUpload keys={videoKeys} />} />
        <Route
          path="/video-recording"
          element={<VideoRecorder keys={videoKeys} />}
        />
      </Routes>
    </div>
  );
}

export default App;
