import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import ProgressBar from '../ProgressBar/ProgressBar';

export default function CVUpload({ keys }) {
  const [cvFile, setCVFile] = useState(undefined);
  const [uploadPercentage, setUploadPercentage] = useState(0);
  const [message, setMessage] = useState(undefined);

  const handleChange = (e) => {
    setCVFile(e.target.files[0]);
  };

  const navigate = useNavigate();

  const upload = async (e) => {
    e.preventDefault();

    if (cvFile === undefined) {
      return setMessage('Debes seleccionar un archivo');
    }

    if (cvFile.type !== 'application/pdf') {
      return setMessage(
        'Solo archivos de tipo .pdf son soportaodos. Por favor elije otro',
      );
    }

    const formData = new FormData();
    formData.append('cv', cvFile);

    try {
      const { data } = await axios.post(
        `/cv/upload?index=${keys.index}`,
        formData,
        {
          headers: {
            'Content-Type': 'application/pdf',
          },

          onUploadProgress: (progressEvent) => {
            setUploadPercentage(
              parseInt(
                Math.round(progressEvent.loaded * 100) / progressEvent.total,
              ),
            );

            setTimeout(() => setUploadPercentage(0), 10000);
          },
        },
      );

      console.log(data);
      navigate('/video-recording', { replace: true });
    } catch (e) {
      console.error(e);
    }
  };

  return (
    <>
      <form onSubmit={(e) => upload(e)}>
        <div className="custom-file">
          <input
            type="file"
            className="custom-file-input"
            name="cv"
            id="cv"
            style={{ marginBottom: '30px' }}
            onChange={handleChange}
          />
        </div>

        <ProgressBar progress={uploadPercentage} />

        {(message && !cvFile) ||
        (message && cvFile.type !== 'application/pdf') ? (
          <h5 style={{ marginTop: '15px' }}>{message}</h5>
        ) : null}

        <input
          type="submit"
          value="Enviar"
          className="btn btn-primary btn-block mt-4"
        />
      </form>
    </>
  );
}
